/* Na.css */

/* Style the navbar background */
.navbar {
    background-color: #f8f9fa;
}

/* Style the brand link */
.navbar-brand {
    font-size: 1.5rem;
    font-weight: bold;
    color: #bb0b1f;
    /* Blue color for the brand link */
    text-decoration: none;
}

.navbar-brand:hover {
    color: #0056b3;
    /* Hover color for the brand link */
}

/* Style the navigation links */
.navbar-nav .nav-item .nav-link {
    color: #333;
    /* Text color for nav links */
    text-decoration: none;
    font-weight: bold;
    /* Make the text bold */
}

.navbar-nav .nav-item .nav-link:hover {
    color: #ff002f;
    /* Hover color for nav links */
}

/* Style the toggler icon */
.navbar-toggler-icon {
    background-color: #2f00ff;
    /* Toggler icon color */
}

/* Media query for mobile screens */
@media (max-width: 768px) {
    .navbar-nav {
        background-color: #f8f9fa;
        text-align: center;
        margin-top: 10px;
    }

    .navbar-nav .nav-item .nav-link {
        padding: 10px 0;
    }
}