/* CheckinList.css */

/* Style the main container */
div {
    text-align: center;
    margin: 20px;
}

/* Style the heading */
h1 {
    font-size: 24px;
    margin-bottom: 20px;
}

/* Style the loading message */
p {
    font-size: 18px;
    margin-top: 20px;
}

/* Style the "No checkins found" message */
.no-checkins-message {
    color: #ff0000;
}

/* Style the table */
table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

/* Style table headers */
th {
    background-color: #f2f2f2;
    font-weight: bold;
    text-align: left;
    padding: 10px;
}

/* Style table cells */
td {
    padding: 10px;
    border: 1px solid #ccc;
}

/* Style the table rows */
tr:nth-child(even) {
    background-color: #f2f2f2;
}

tr:hover {
    background-color: #e0e0e0;
}