/* NoticeList.css */
.notice-board {
    background-color: #f9f9f9;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    margin: 10px;
}

.notice-item {
    background-color: #fff;
    padding: 10px;
    margin: 10px 0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border: 1px solid #ccc;
    border-radius: 5px;
}

.notice-title {
    font-size: 24px;
    margin: 0;
}

.notice-content {
    font-size: 16px;
}

.notice-image {
    max-width: 100%;
    height: auto;
}

.read-more-link {
    text-decoration: none;
    color: #007bff;
}