.id-card-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.id-card {
    width: 300px;
    background-color: #f1f1f1;
    border: 2px solid #333;
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.id-card-header {
    text-align: center;
}

.id-card-header h2 {
    font-size: 24px;
    margin: 0;
}

.id-card-header p {
    font-size: 16px;
    margin: 5px 0;
}

.id-card-content p {
    margin: 5px 0;
}

/* Add more styles as needed to achieve the desired ID card appearance */