/* Style the card container as a flex container */
.card-container {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 20px;
}

/* Style the individual card components */
.card {
    width: 18rem;
    margin-right: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
    display: flex;
    flex-direction: column;

    /* Display card contents in a column layout */
    &:hover {
        transform: scale(1.05);
    }

    /* Set a fixed height for the card images */
    .card-img-top {
        max-width: 100%;
        height: 200px;
        object-fit: cover;
    }

    .card-text {
        font-size: 14px;
    }

    /* Media query for mobile screens */
    @media (max-width: 768px) {
        .card-container {
            flex-direction: column;
            align-items: center;
        }

        .card {
            float: left;
            width: 33.333%;
            padding: .75rem;
            margin-bottom: 2rem;
            border: 0;
        }

        .card>img {
            margin-bottom: .75rem;
        }

        .card-text {
            font-size: 85%;
        }
    }
}

/* Card.css */
.card-container-outer {
    margin-bottom: 6px;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 16px;
    margin: 20px;
}