/* Header.css */

/* Styling for the image container */
.img-container {
    text-align: center;
    /* Center the image horizontally */
    position: relative;
    width: 100%;
    /* Make the container full width of the screen */
}

/* Styling for the background effect */
.img-container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, #ff5733, #b025d6);
    opacity: 0.7;
    z-index: -1;
    filter: blur(10px);
    transition: opacity 0.3s ease;
}

/* Background hover effect */
.img-container:hover::before {
    opacity: 0.9;
}

.img-container img {
    max-width: 100%;
    max-height: 100%;
    /* Ensure the image fits the screen and maintains aspect ratio */
    position: relative;
    z-index: 1;
    transition: transform 0.3s ease;
    /* Smooth transition for hover effect */
}

/* Image hover effect */
.img-container:hover img {
    transform: scale(1.1);
    /* Adjust the scale factor for the hover effect */
}

/* Media query for mobile screens */
@media (max-width: 768px) {
    .img-container {
        text-align: center;
    }

    .img-container img {
        max-width: 100%;
        max-height: auto;
        position: relative;
        z-index: 1;
    }
}